import { combineReducers } from 'redux'

const challenges = (state=[], action) => {
  switch (action.type) {
    case "ADD_RANDOM_CHALLENGE":
      console.log(action);
      return [
        ...state,
        {
          id: action.id,
          text: action.text
        }
      ];
      case "GET_CHALLENGES": 
        return [...state, ...action.newChallenges ] ;      

      default:
        return state
  }
}

const reducers = combineReducers({
  challenges
})

export default reducers


