import axios from 'axios'

//Trae las versiones 
//https://global.api.riotgames.com/api/lol/static-data/NA/v1.2/versions?api_key=5b90a73b-948a-4c72-a2d0-ba1eb1e443af

export function addRandomChallenge(text,id) {
	return { type: 'ADD_RANDOM_CHALLENGE', text, id}
}

export function fetchChamps() {
  return function (dispatch) {
    return axios.get('https://global.api.riotgames.com/api/lol/static-data/NA/v1.2/champion?api_key=5b90a73b-948a-4c72-a2d0-ba1eb1e443af')
      .then((response) => {		  
 				var text = 'Win ' + Math.floor((Math.random() * 3) + 1) + ' matche(s) with ';        
        var totalChampions = Object.keys(response.data.data).length;
        var selectedChampion = Math.floor((Math.random() * totalChampions));

        var champName = Object.keys(response.data.data)[selectedChampion];
        text+= champName;

        var id = response.data.data[champName].id;
				console.log(text);
      
        dispatch(addRandomChallenge(text, id));
      })
      .catch((err) => {
        console.log('error obtaining new challenge',err);
      });             
  }
}

export function getChallenges() {
	var newChallenges =  [
            { id: 1, text: 'Ganar 5 partidas con Teemo'},
            { id: 2, text: 'Ganar 2 partidas con Yasuo'},
            { id: 3, text: 'Ganar 4 partidas con Jinx'},
            { id: 4, text: 'Ganar 1 partidas con Galio'}
          ]
  return { type: 'GET_CHALLENGES', newChallenges }
}

export function getChampions() {
  return { type: 'GET_CHAMPIONS' }
}


