import React, { Component } from 'react';
import './ChallengeList.css';

class AddChallenge extends Component {

	render(){			
		return (
			<div >
				<button onClick={this.props.fetchChamps}>Add random challenge!</button>
      </div>
		)
	}
}

export default AddChallenge;


